import { createGlobalStyle } from "styled-components";

const GlobalStyle = createGlobalStyle`

  *{
    padding:0;
    margin:0;
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
    border: none;
    font-family: 'Poppins', sans-serif;
    user-select: none;
  }
  
  
  u{
    text-decoration: underline;
  }
  
  *:link,
  *:visited{
    color:unset;
  }
  
  *::-webkit-scrollbar {
    width: 0;
  }
  
  *:focus{
    outline:none;
  }

  *:disabled{
    cursor: not-allowed;
  }
  
  input{
    outline: none;
    user-select: auto;
    background: unset;
    
    
    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
  }


  label{
    cursor: pointer;
  }

  button{
    background: none;
    cursor: pointer;
  }

  textarea{
    resize: none;
    user-select:auto;
  }

  &#BroadBox {
    background: #373737;
  }

  .noSpace{
    width: 0;
    height: 0;
    position: absolute;
  }

  .defaultPopup {
    width: 500px;
    padding: 40px;
    background: #fff;
    box-shadow: 0px 3px 6px #00000029;
    border-radius: 15px;
    top: 50%;
    left: 50%;
    position: fixed;
    transform: translate(-50%, -50%);
    z-index: 6;
  }





  /* effect */
  .particles {
    position: fixed;
    top: 0;
    left: 0;
    pointer-events: none;

    .particle {
      position: absolute;
      transition: all 5s ease-out;

      &.circular {
        background-color: var(--color);
        width: var(--size);
        height: var(--size);
        border-radius: var(--size);
        transform: translate(var(--x), var(--y));
      }

      &.rectangular {
        background-color: var(--color);
        width: var(--size);
        height: var(--size);
        transform: translate(var(--x), var(--y)) rotateX(var(--rotate))
          rotateY(var(--rotate));
      }

      &.squiggle {
        stroke: var(--color);
        stroke-width: 15px;
        stroke-linecap: round;
        overflow: visible;
        width: var(--size);
        height: var(--size);
        transform: translate(var(--x), var(--y)) rotateY(var(--rotate));
      }
    }
  }

  .button {
    height: 40px;
    width: 200px;
    font-family: Roboto;
    background-color: #34495e;
    color: white;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.2s ease-out;
    user-select: none;

    &:hover {
      background-color: #8e44ad;
    }

    .popper {
      margin-right: 20px;
      margin-left: -20px;
      width: 64px;
      height: 64px;
      background-image: url("data:image/png;base64,");
    }
  }
`;

export default GlobalStyle;
